import { Route, Routes } from "react-router-dom";
import Error from "./pages/error/error";
import HomePage from "./pages/homepage/homepage";

import { useContext } from "react";
import { AuthContext } from "./context/authContext";
import Login from "./pages/login/login";
import Profile from "./pages/profile/profile";
import Header from "./container/header/header";
import Footer from "./container/footer/footer";
import { ProductItem } from "./container/productItem/productItem";
import { ProfileItem } from "./components/profileItem/profileItem";
import SingleProduct from "./pages/singleProduct/singleProduct";
import Ads from "./pages/adsPage/ads";
import AddProduct from "./pages/addProduct/addProduct";
import "./App.css";

function App() {
  const { token } = useContext(AuthContext);
  return (
    <>
      <Header />
      <Routes>
        <Route path="/" element={<HomePage />}>
          <Route path="" element={<ProductItem />} />
          <Route
            path="/best"
            element={
              <h2 style={{ padding: "50px" }} className="text__blinker">
                Лучщие предложение
              </h2>
            }
          />
        </Route>

        <Route path="/addProduct" element={<AddProduct />} />
        <Route path="/profile" element={<Profile />}>
          <Route path="" element={<ProfileItem />} />
          <Route
            path="message"
            element={<h2 className="text__blinker">Сообщения</h2>}
          />
          <Route
            path="account"
            element={<h2 className="text__blinker">Платежи и счёт</h2>}
          />
          <Route
            path="settings"
            element={<h2 className="text__blinker">Настройки</h2>}
          />
          <Route
            path="business"
            element={<h2 className="text__blinker">Мой бизнес</h2>}
          />
        </Route>

        <Route path="/singlePage/:id" element={<SingleProduct />} />
        <Route path="/ads" element={<Ads />}></Route>

        {token ? (
          <>
            <Route path="*" element={<Error />} />
          </>
        ) : (
          <>
            <Route path="/login" element={<Login />} />
          </>
        )}
      </Routes>

      <Footer />
    </>
  );
}

export default App;
