import { useRef } from "react";
import { useContext } from "react";
import { AuthContext } from "./../../context/authContext";
import Header from "./../../container/header/header";
import { useNavigate } from "react-router-dom";
import "./login.scss";

const Login = () => {
  const { setToken } = useContext(AuthContext);
  const navigate = useNavigate();
  const nameRef = useRef();
  const passwordRef = useRef();
  const handleSubmit = (e) => {
    e.preventDefault();

    console.log({
      email: nameRef.current.value,
      password: passwordRef.current.value,
    });
    fetch("https://reqres.in/api/login", {
      method: "POST",
      body: JSON.stringify({
        email: nameRef.current.value,
        password: passwordRef.current.value,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((data) => {
        setToken(data);
        navigate("/");
      });
  };

  return (
    <>
      <div className="login">
        <div className="login__wrapper container">
          <div className="login__left">
            <h3 className="login__left-title">Ваша регистрация завершена!</h3>

            <p className="login__left-text">
              Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен
              в виде смс на ваше телефонный номер
            </p>
            <form onSubmit={handleSubmit} className="login__form">
              <label htmlFor="input-email" className="login__form-label">
                Введите email или номер телефона
              </label>
              <input
                ref={nameRef}
                type="email"
                id="input-email"
                placeholder="99891 167 27 23"
                className="login__form-input"
              />

              <label htmlFor="input-name" className="login__form-label">
                Пароль
              </label>
              <input
                ref={passwordRef}
                type="password"
                id="input-name"
                placeholder="***********"
                className="login__form-input"
              />

              <button className="login__form-btn">Далее</button>
            </form>

            <h3 className="login__or">или</h3>

            <a href="#" className="login__password">
              Восстоновить ваше пароль
            </a>
          </div>

          <div className="login__right">
            <div className="login__right-wrapper">
              <h4 className="login__right-title">kivi</h4>
              <h5 className="login__right-text">
                Откройте для себя целую плошадку для продаже
              </h5>
              <button className="login__right-btn">Как это делать?</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
