import {
  Car,
  Construction,
  Electronic,
  Game,
  Gift,
  Home,
  Job,
  Man,
  Mountain,
} from "../../assets/icons/icons";
// import Gift from "./../../assets/images/Gifts.svg";
import "./homepage.scss";
import { NavLink, Outlet } from "react-router-dom";
import CenterMode from "../../components/swiper/swiper";
import { SliderCard } from "../../components/sliderCard/siliderCard";
import { ProductItemLimited } from "../../container/productItem/productItem";

function HomePage() {
  return (
    <>
      <div className="homepage__card-wrapper container">
        <SliderCard title="Автомобили" bgColor="primary" icon={<Car />} />
        <SliderCard title="Недвижимость" bgColor="cyan" icon={<Home />} />
        <SliderCard
          title="Электроника"
          bgColor="orange"
          icon={<Electronic />}
        />
        <SliderCard title="Работа" bgColor="yellow" icon={<Job />} />
        <SliderCard title="Личные вещи" bgColor="lightOrange" icon={<Gift />} />
        <SliderCard title="Дом и Сад" bgColor="cyan" icon={<Mountain />} />
        <SliderCard
          title="Стройка и ремонт"
          bgColor="primary"
          icon={<Construction />}
        />
        <SliderCard
          title="Хобби и Спорт"
          bgColor="lightOrange"
          icon={<Game />}
        />
        <SliderCard title="Бизнес и услуги" bgColor="orange" icon={<Man />} />
      </div>
      <div className="homepage container">
        <div className="homepage__top">
          <NavLink
            style={{ borderBottom: "2px solid blue", paddingBottom: "10px" }}
            className="active"
            to="/"
            end
          >
            <span className="homepage__top-heading active">
              Новые объявления
            </span>
          </NavLink>
          <NavLink to="/best">
            <span className="homepage__top-heading">Лучщие предложение</span>
          </NavLink>
          <Outlet />
        </div>
      </div>
      <CenterMode />
      <div className="homepage">
        <div className="homepage__top container">
          <a
            style={{ borderBottom: "2px solid blue", paddingBottom: "10px" }}
            className="homepage__top-heading active"
          >
            Просмотренные
          </a>
          <a className="homepage__top-heading">Выбор редакции</a>
          <a className="homepage__top-heading">Сниженные цены</a>
          <ProductItemLimited />
        </div>
      </div>
    </>
  );
}

export default HomePage;
