import "./lookingSlider.scss";

import React, { Component } from "react";
import Slider from "react-slick";

export default class SwipeToSlide extends Component {
  render() {
    const settings = {
      className: "center",
      infinite: true,
      centerPadding: "60px",
      slidesToShow: 5,
      swipeToSlide: true,
      afterChange: function (index) {
        console.log(
          `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
        );
      },
    };
    return (
      <div className="lookingSlider">
        <div className="container">
          <h2 className="lookingSlider__title">СЕЙЧАС ИЩУТ</h2>
          <Slider {...settings}>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
            <div className="lookingSlider__wrapper">
              <h2 className="lookingSlider__heading">
                купить Мясная продукция
              </h2>
              <p className="lookingSlider__text">2 часа назад</p>
            </div>
          </Slider>
        </div>
      </div>
    );
  }
}
