import "./addProduct.scss";

import {
  Car,
  Home,
  Electronic,
  Job,
  Gift,
  Mountain,
  Construction,
  Game,
  Man,
} from "./../../assets/icons/icons";
import { SliderCard } from "../../components/sliderCard/siliderCard";
import { useRef } from "react";

const AddProduct = () => {
  const numberOfRoom = useRef();
  const price = useRef();
  const roomNineHundred = useRef();
  const roomNinety = useRef();
  const floor = useRef();
  const height = useRef();
  const landArea = useRef();
  const phoneNumber = useRef();
  const eMail = useRef();
  const contact = useRef();

  const handleProductLog = (e) => {
    console.log({
      product: numberOfRoom.current.value,
      price: price.current.value,
      roomNineHundred: roomNineHundred.current.value,
      numberOfRoom: numberOfRoom.current.value,
      product: roomNinety.current.value,
      floor: floor.current.value,
      height: height.current.value,
      landArea: landArea.current.value,
      phoneNumber: phoneNumber.current.value,
      eMail: eMail.current.value,
      contact: contact.current.value,
    });
  };
  return (
    <>
      <div className="addProduct container">
        <h2 className="addProduct__title">Добавить обьявления бесплатно</h2>
        <h4 className="addProduct__subtitle">
          Для добавления вашего обьявлении вы должны выбрать категорию
        </h4>

        <div className="addProduct__card-wrapper">
          <div className="addProduct__card">
            <SliderCard title="Автомобили" bgColor="primary" icon={<Car />} />
          </div>
          <div className="addProduct__single-card">
            <SliderCard title="Недвижимость" bgColor="cyan" icon={<Home />} />
          </div>
          <div className="addProduct__card">
            <SliderCard
              title="Электроника"
              bgColor="orange"
              icon={<Electronic />}
            />
          </div>
          <div className="addProduct__card">
            <SliderCard title="Работа" bgColor="yellow" icon={<Job />} />
          </div>
          <div className="addProduct__card">
            <SliderCard
              title="Личные вещи"
              bgColor="lightOrange"
              icon={<Gift />}
            />
          </div>
          <div className="addProduct__card">
            <SliderCard title="Дом и Сад" bgColor="cyan" icon={<Mountain />} />
          </div>
          <div className="addProduct__card">
            <SliderCard
              title="Стройка и ремонт"
              bgColor="primary"
              icon={<Construction />}
            />
          </div>
          <div className="addProduct__card">
            <SliderCard
              title="Хобби и Спорт"
              bgColor="lightOrange"
              icon={<Game />}
            />
          </div>
          <div className="addProduct__card">
            <SliderCard
              title="Бизнес и услуги"
              bgColor="orange"
              icon={<Man />}
            />
          </div>
        </div>

        <div className="addProduct__edit">
          <h4 className="addProduct__edit-title">Вы выбрали:</h4>
          <h4 className="addProduct__edit-address">Недвижимость »</h4>
          <h4 className="addProduct__edit-address">Дома »</h4>
          <h4 className="addProduct__edit-address">Аренда долгосрочная</h4>

          <button className="addProduct__edit-btn">Изменить</button>
        </div>

        <form className="addProduct__form">
          <div className="addProduct__form-top">
            <div className="addProduct__label-wrapper">
              <label className="addProduct__label">Цена</label>
              <input
                ref={price}
                className="addProduct__input"
                placeholder="400 000"
              />
            </div>
            <select disabled className="addProduct__select">
              <option className="addProduct__option">Сум</option>
            </select>

            <input type="checkbox" className="addProduct__input-check" />
            <label className="addProduct__label-check">Договорная</label>
          </div>

          <div className="addProduct__form-top">
            <div className="addProduct__label-wrapper">
              <label className="addProduct__label">Количество комнат*</label>
              <input
                ref={numberOfRoom}
                placeholder="4"
                className="addProduct__input"
              />
            </div>

            <input
              ref={roomNineHundred}
              placeholder="900"
              className="addProduct__input"
            />

            <input
              ref={roomNinety}
              placeholder="90"
              className="addProduct__input"
            />
          </div>

          <h2 className="addProduct__label">Расположение*</h2>

          <div className="addProduct__block">
            <div className="addProduct__block-top">
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">В городе</label>
              </div>
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">Вдоль трассы</label>
              </div>
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">
                  В дачном массиве
                </label>
              </div>
            </div>
            <div className="addProduct__block-top">
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">В пригороде</label>
              </div>
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">
                  Возле водоема, реки
                </label>
              </div>
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">
                  На закрытой территории
                </label>
              </div>
            </div>
            <div className="addProduct__block-bottom">
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">
                  В сельской местности
                </label>
              </div>
              <div className="addProduct__check-div">
                <input type="checkbox" className="addProduct__input-check" />
                <label className="addProduct__label-check">В предгорьях</label>
              </div>
            </div>
          </div>

          <div className="addProduct__form-top">
            <div className="addProduct__label-input">
              <label className="addProduct__label">Этажность дома*</label>
              <input
                ref={floor}
                placeholder="4"
                className="addProduct__input"
              />
            </div>

            <div className="addProduct__label-input">
              <label className="addProduct__label">Высота потолков</label>
              <input
                ref={height}
                placeholder="2m"
                className="addProduct__input"
              />
            </div>

            <div className="addProduct__label-input">
              <label className="addProduct__label">Площадь участка*</label>
              <input
                ref={landArea}
                placeholder="342"
                className="addProduct__input"
              />
            </div>
          </div>

          <h2 className="addProduct__label">Состояние дома</h2>

          <div className="addProduct__block-top">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">
                Авторский проект
              </label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Требует ремонта</label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">
                Черновая отделка
              </label>
            </div>
          </div>
          <div className="addProduct__block-top">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Средний ремонт</label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">
                Предчистовая отделка
              </label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Под снос</label>
            </div>
          </div>
          <div className="addProduct__block-bottom">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Евроремонт</label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Не достроен</label>
            </div>
          </div>

          <div className="addProduct__select-block">
            <div className="addProduct__block-info">
              <label className="addProduct__label">Тип дома</label>
              <select disabled className="addProduct__select">
                <option>Коттедж</option>
              </select>
            </div>
            <div className="addProduct__block-info">
              <label className="addProduct__label">
                Есть возможность подключения
              </label>
              <select disabled className="addProduct__select">
                <option>Сум</option>
              </select>
            </div>
          </div>

          <h2 className="addProduct__label">Отопление</h2>

          <div className="addProduct__block-top">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Центральное</label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">
                На жидком топлив
              </label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Без отопления</label>
            </div>
          </div>
          <div className="addProduct__block-top">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">На газе</label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Электрическое</label>
            </div>
          </div>
          <div className="addProduct__block-bottom">
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">
                На твердом топливе
              </label>
            </div>
            <div className="addProduct__check-div">
              <input type="checkbox" className="addProduct__input-check" />
              <label className="addProduct__label-check">Смешанное</label>
            </div>
          </div>

          <h2 className="addProduct__label">Электричество</h2>

          <h2 className="addProduct__middle-heading">
            Есть возможность подключения
          </h2>

          <div className="form-bottom">
            <h2 className="addProduct__label">9000 знаков осталос</h2>

            <div className="img-loader-block">
              <div className="img-loader-block-top">
                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>
              </div>

              <div className="img-loader-block-top">
                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>

                <div className="img-loader">
                  <input type="file" className="input-file" />
                  <label className="input-file-label"></label>
                </div>
              </div>
            </div>
          </div>

          <div className="additional-footer">
            <h3 className="additional-footer__title">Местоположение объекта</h3>

            <div className="addProduct__select-block">
              <div className="addProduct__block-info">
                <label className="addProduct__label">Регион</label>
                <select disabled className="addProduct__select">
                  <option>Ташкент</option>
                </select>
              </div>
              <div className="addProduct__block-info">
                <label className="addProduct__label">Город / Район</label>
                <select disabled className="addProduct__select">
                  <option>Мирза Улугбекский район</option>
                </select>
              </div>
            </div>

            <div className="iframe-block">
              <iframe
                style={{ marginBottom: "22px" }}
                src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d16928.009177961183!2d69.25971725068385!3d41.40715176379929!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sru!2s!4v1665032297131!5m2!1sru!2s"
                width="1140"
                height="450"
                allowfullscreen=""
                loading="lazy"
                referrerpolicy="no-referrer-when-downgrade"
              ></iframe>

              <div className="overview-user">
                <div
                  style={{ maxWidth: "309px" }}
                  className="addProduct__label-input"
                >
                  <label className="addProduct__label">Телефонный номер</label>
                  <input
                    ref={phoneNumber}
                    type="phone"
                    placeholder="+99894 680 28 05"
                    className="addProduct__input"
                  />
                </div>

                <div
                  style={{ maxWidth: "309px" }}
                  className="addProduct__label-input"
                >
                  <label className="addProduct__label">Email-адрес</label>
                  <input
                    ref={eMail}
                    type="phone"
                    placeholder="+99894 680 28 05"
                    className="addProduct__input"
                  />
                </div>

                <div
                  style={{ maxWidth: "309px" }}
                  className="addProduct__label-input"
                >
                  <label className="addProduct__label">Контактный лицо</label>
                  <input
                    ref={contact}
                    type="phone"
                    placeholder="Зухриддин"
                    className="addProduct__input"
                  />
                </div>

                <div className="flex-blockk">
                  <button
                    type="button"
                    onClick={handleProductLog}
                    className="add-new"
                  >
                    Добавить обьявления бесплатно
                  </button>

                  <a href="#" className="add-new-view">
                    Предпросмотр
                  </a>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};

export default AddProduct;
