import { Link } from "react-router-dom";
import "./productCard.scss";

function ProductCard({ img, title, date, price, id }) {
  return (
    <>
      <Link to={`/singlePage/${id}`} className="productCard__item">
        <div>
          <img className="productCard__img" src={img} alt="Product Image" />
          <h3 className="productCard__heading">{title}</h3>
        </div>
        <div>
          <p className="productCard__date">{date}</p>
          <p className="productCard__price">{price * 11500} cум</p>
        </div>
      </Link>
    </>
  );
}

export default ProductCard;
