import "./profileCard.scss";

import Profile_Img from "./../../assets/images/profile__img.png";
import Profile__dots from "./../../assets/images/profile__dots.svg";
function ProfileCard({
  title,
  price1,
  price2,
  data1,
  data2,
  data3,
  view,
  tel,
  saved,
}) {
  return (
    <div className="profileCard">
      <div className="profileCard__top">
        <img
          src={Profile_Img}
          alt="ProfileImg"
          className="profileCard__top-img"
        />
        <h3 className="profileCard__top-title">{title}</h3>

        <div className="profileCard__price">
          <h3 className="profileCard__price-title">{price1}</h3>
          <h3 className="profileCard__price-title">{price2}</h3>
        </div>

        <div className="profileCard__date">
          <h4 className="profileCard__date-item">{data1}</h4>
          <h4 className="profileCard__date-item">{data2}</h4>
          <h4 className="profileCard__date-item">{data3}</h4>
        </div>

        <button className="profileCard__btn">Поднять</button>
        <button className="profileCard__btn btn--added">Рекламировать</button>

        <div className="profileCard__more-img">
          <img className="profileCard__more-bg" src={Profile__dots} />
        </div>
      </div>

      <div className="profileCard__bottom">
        <h4 className="profileCard__bottom-stat">Статистика</h4>
        <h4 className="profileCard__date-item">Просмотры:{view}</h4>
        <h4 className="profileCard__date-item">Тел.:{tel}</h4>
        <h4 className="profileCard__date-item">В Избранном:{saved}</h4>
      </div>
    </div>
  );
}

export default ProfileCard;
