import SwipeToSlide from "../../components/lookingSlider/lookingSlider";
import {
  ProductItem,
  ProductItemLimited,
} from "../../container/productItem/productItem";
import "./ads.scss";

function Ads() {
  return (
    <div className="ads">
      <div className="ads__wrapper">
        <div className="container">
          <div className="ads__top">
            <input
              type="search"
              className="ads__top-input"
              placeholder="Введите название обявления или  выберите из списка"
            />
          </div>

          <div className="ads__top-navigate">
            <button className="ads__top-btn interes">Интерсное</button>

            <button className="ads__top-btn top">Топ</button>

            <button className="ads__top-btn perfect">
              Отличные предложение
            </button>

            <button className="ads__top-btn g-price">Супер цена</button>

            <button className="ads__top-btn place-town">г.Ташкент</button>

            <button className="ads__top-btn place-town">г.Термез</button>

            <button className="ads__top-btn place-town">г.Самарканд</button>
          </div>

          <div className="ads__top-info">
            <div className="ads__info-left">
              <h3 className="ads__left-title">Вы сейчас в рубрике</h3>
            </div>

            <div className="ads__top-right">
              <div className="ads__right-info">
                <h3 className="ads__right-title">Электроника</h3>
              </div>

              <div className="ads__right-info">
                <h3 className="ads__right-title">Мониторы</h3>
              </div>
            </div>
          </div>

          <div className="ads__top-price">
            <div className="ads__price-left">
              <h3 className="ads__left-title">Выберите цену от и до</h3>
            </div>

            <div className="ads__price-right">
              <div className="ads__price-range--wrapper">
                <span className="rangeValues"></span>
                <input type="range" step="1" min="1" max="100" />
              </div>
            </div>
          </div>

          <div className="ads__form">
            <div className="ads__form-left">
              <select disabled className="ads__form-select">
                <option>Состояние</option>
              </select>

              <select disabled className="ads__form-select">
                <option>Тип объявщика</option>
              </select>

              <select disabled className="ads__form-select">
                <option>Ташкент Мирза улугбекский район</option>
              </select>
            </div>

            <div>
              <button className="ads__form-btn">Применить</button>
            </div>
          </div>
        </div>
      </div>

      <div className="container">
        <ProductItem />
      </div>

      <div className="now-wanted-slider">
        <SwipeToSlide />
      </div>
      <div className="ads__bottom-info container">
        <h3 className="ads__bottom-card--heading">САМОЕ ИНТЕРЕСНОЕ</h3>
        <ProductItemLimited />
      </div>
    </div>
  );
}

export default Ads;
