// export default SliderCard;

export const SliderCard = ({ icon, title, bgColor }) => {
  return (
    <>
      <div className="card">
        <div className={`card__img ${bgColor}`}>{icon}</div>
        <p className="card__text">{title}</p>
      </div>
    </>
  );
};
