import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Swiper_1 from "./../../assets/images/swiper_1.png";
import Swiper_2 from "./../../assets/images/swiper-2.png";
import Swiper_3 from "./../../assets/images/swiper-3.png";
import Swiper_4 from "./../../assets/images/swiper_4.png";
import "./swiper.scss";
import React, { Component } from "react";
import Slider from "react-slick";

export default class CenterMode extends Component {
  render() {
    const settings = {
      className: "center swiper__info",
      centerMode: true,
      infinite: true,
      centerPadding: "60px",
      slidesToShow: 3,
      speed: 500,
    };
    return (
      <div className="swiper">
        <div className="container">
          <div className="swiper__wrapper">
            <Slider {...settings}>
              <div>
                <img src={Swiper_1} alt="" />
              </div>
              <div>
                <img src={Swiper_2} alt="" />
              </div>
              <div>
                <img src={Swiper_3} alt="" />
              </div>
              <div>
                <img src={Swiper_4} alt="" />
              </div>
            </Slider>
          </div>
        </div>
      </div>
    );
  }
}
