import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import { ProductItemLimited } from "../../container/productItem/productItem";
import "./singleProduct.scss";

function SingleProduct() {
  const params = useParams();
  const [products, setProducts] = useState({
    isFetched: false,
    data: [],
    error: null,
  });

  useEffect(() => {
    fetch(`https://fakestoreapi.com/products/${params.id}`)
      .then((res) => res.json())
      .then((data) => {
        setProducts({
          isFetched: true,
          data: data,
          error: false,
        });
        // console.log(data);
      })
      .catch((err) => {
        setProducts({
          isFetched: false,
          data: [],
          error: err,
        });
      });
  }, []);
  return (
    <>
      <div className="singlePage ">
        <div className="container">
          <div className="singlePage__top">
            <h4 className="singlePage__top-navigate">Объявления</h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate">Ташкент Недвижимост</h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate">Ташкент Квартиры</h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate">Ташкент Продажа</h4>
            <span>/</span>
            <h4 className="singlePage__top-navigate">Новостройки Ташкент</h4>
          </div>

          <div className="singlePage__center-wrapper">
            <h2 className="singlePage__center-title">{products.data.title}</h2>

            <div className="singlePage__product">
              <div className="singlePage__product-left">
                <img
                  style={{ marginTop: "80px" }}
                  src={products.data.image}
                  width="500"
                  height="auto"
                  alt="image"
                />
              </div>

              <div className="singlePage__product-right">
                <div className="singlePage__right-top">
                  <h4 className="singlePage__top-heading">Артикул: 4418970</h4>

                  <h4 className="singlePage__top-heading">
                    обновлено: 28.04.17 добавлено: 28.04.17
                  </h4>
                </div>

                <h3 className="singlePage__top-price">
                  {products.data.price} у.е.
                </h3>

                <a href="#" className="singlePage__top-link">
                  Ташкент, Ташкентская область, Юнусабадский район
                </a>

                <div className="singlePage__customer-info">
                  <h3 className="singlePage__info-phone">998 91 166 ****</h3>
                  <button className="singlePage__info-btn">
                    Покозать польностю
                  </button>
                </div>

                <div className="singlePage__customer-info">
                  <h3 className="singlePage__info-phone">998 91 166 ****</h3>
                  <button className="singlePage__info-btn">
                    Покозать польностю
                  </button>
                </div>

                <div className="singlePage__auth-info">
                  <h4 className="singlePage__auth-heading">
                    Автор обьявлении:
                  </h4>
                  <h5 className="singlePage__auth-title">Зухриддин Темиров</h5>
                </div>

                <div className="singlePage__contact-info">
                  <button className="singlePage__contact-btn">
                    Написать автору
                  </button>
                  <button className="singlePage__contact-btn activeBTN">
                    Предложить свою цену
                  </button>
                </div>

                <div className="singlePage__table">
                  <div className="singlePage__table-left">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Количество комнат:
                      </h4>
                      <h4 className="singlePage__table-text">6</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Этажность дома:
                      </h4>
                      <h4 className="singlePage__table-text">6</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">Планировка</h4>
                      <h4 className="singlePage__table-text special">
                        Раздельная
                      </h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">Санузел</h4>
                      <h4 className="singlePage__table-text special">
                        2 санузла и более
                      </h4>
                    </div>
                  </div>
                  <div className="table-center">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Общая площадь:
                      </h4>
                      <h4 className="singlePage__table-text">40 м2</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Состояние квартиры:
                      </h4>
                      <h4 className="singlePage__table-text">Первая сдача</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Год постройки/сдачи
                      </h4>
                      <h4 className="singlePage__table-text special">
                        Сдача в 2017
                      </h4>
                    </div>
                    <div className="singlePage__table-item small">
                      <h4 className="singlePage__table-title">Меблирована</h4>
                      <h4 className="singlePage__table-text special">Да</h4>
                    </div>
                  </div>
                  <div className="table-right">
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">Этаж:</h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">Этаж:</h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">Ремонт</h4>
                      <h4 className="singlePage__table-text">
                        Авторский проект
                      </h4>
                    </div>
                    <div className="singlePage__table-item">
                      <h4 className="singlePage__table-title">
                        Высота потолков
                      </h4>
                      <h4 className="singlePage__table-text">32</h4>
                    </div>
                  </div>
                </div>

                <div className="singlePage__table-bottom">
                  <div className="singlePage__table-item">
                    <h4 className="singlePage__table-title">Рядом есть</h4>
                    <h4 className="singlePage__table-text special">
                      Больница, поликлиника, Детская, площадка, Детский сад,
                      Остановки, Парк, зелёная зона, Развлекательные заведения,
                      Рестораны, кафе
                    </h4>
                  </div>
                </div>

                <div className="singlePage__about">
                  <h4 className="singlePage__about-text">
                    {products.data.description}
                  </h4>
                </div>

                <div className="add-info">
                  <h4 className="add-info__left">
                    Просмотры:
                    <span>10958</span>
                  </h4>

                  <a href="#" className="add-info__right">
                    Пожаловатся
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div
            style={{ marginBottom: "21px" }}
            className="homepage__top container"
          >
            <NavLink
              style={{ borderBottom: "2px solid blue" }}
              className="homepage__top-heading active"
              to="/"
              end
            >
              Просмотренные
            </NavLink>
            <a className="homepage__top-heading">Выбор редакции</a>
            <a className="homepage__top-heading">Сниженные цены</a>
          </div>
          <ProductItemLimited />
        </div>
      </div>
    </>
  );
}

export default SingleProduct;
