import { useContext, useRef } from "react";
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import { Link, NavLink, useNavigate } from "react-router-dom";
import { AuthContext } from "../../context/authContext";
import Logo from "./../../assets/images/logo.svg";
import Profile_page from "./../../assets/images/Profile_Page.jpg";

import "./header.scss";
import { Languages } from "../../languages/language";

i18n.use(initReactI18next).init({
  resources: Languages,
  lng: "ru",
  fallbackLng: "ru",

  interpolation: {
    escapeValue: false,
  },
  lng: localStorage.getItem("lng") || "ru",
});

export const Header = () => {
  const uz = useRef(null);
  const ru = useRef(null);
  const { t, i18n } = useTranslation();

  const handleChangeLanguageUz = (lng) => {
    i18n.changeLanguage(lng);
    localStorage.setItem("lng", lng);
    uz.current.classList.add("lang-active");
    ru.current.classList.remove("lang-active");
  };
  const handleChangeLanguageRu = (lng) => {
    i18n.changeLanguage(lng);
    localStorage.setItem("lng", lng);
    ru.current.classList.add("lang-active");
    uz.current.classList.remove("lang-active");
  };

  const { token, setToken } = useContext(AuthContext);
  const navigate = useNavigate();
  const handleLogOut = () => {
    setToken(null);
    navigate("/");
  };

  return (
    <>
      <header className="header">
        <div className="container">
          <div className="header__container">
            <div className="header__info">
              <Link to="/">
                <img src={Logo} alt="Kiwi Icon" />
              </Link>
              <p className="header__info-text">{t("title")}</p>
            </div>

            <nav className="header__navbar">
              <ul className="header__list">
                <li className="header__item">
                  <NavLink to="/ads" className="header__item-link">
                    {t("ads")}
                  </NavLink>
                </li>
                <li className="header__item">
                  <NavLink className="header__item-link" to="/addProduct">
                    {t("market")}
                  </NavLink>
                </li>
                <li className="header__item">
                  <a className="header__item-link">{t("busnes")}</a>
                </li>
                <li className="header__item">
                  <a className="header__item-link" href="#">
                    {t("help")}
                  </a>
                </li>
              </ul>
            </nav>

            <div className="header__lang-wrapper">
              <button
                ref={ru}
                onClick={() => handleChangeLanguageRu("ru")}
                className="header__lang-btn"
              >
                Рус
              </button>
              <span className="header__lang-line"></span>
              <button
                ref={uz}
                onClick={() => handleChangeLanguageUz("uz")}
                className="header__lang-btn"
              >
                O'z
              </button>
            </div>

            <div className="header__right-wrapper">
              {token ? (
                <>
                  <div className="header__right-wrapper">
                    <Link to="/profile" className="header__right-auth">
                      <p className="header__right-title">Mahmudxo'jayev</p>
                      <img className="header__right-img" src={Profile_page} />
                    </Link>
                    <button className="header__log-out" onClick={handleLogOut}>
                      Log out
                    </button>
                  </div>
                </>
              ) : (
                <>
                  <Link to="/login" className="header__right-login">
                    {t("login")}
                  </Link>
                  <span className="header__right-line"></span>
                  <Link to="/login" className="header__right-sign">
                    {t("register")}
                  </Link>
                </>
              )}
            </div>
          </div>
          <div className="header__bottom">
            <form className="header__bottom-left">
              <select disabled className="header__bottom-select">
                <option>Любая категория</option>
              </select>

              <input
                type="search"
                className="header__bottom-input"
                placeholder="Что будем искать ?"
              />

              <select disabled className="header__bottom-select next">
                <option>По всей Узбекистану</option>
              </select>

              <a href="#" className="header__bottom-btn">
                Найти
              </a>
            </form>

            <div className="header__bottom-right">
              <a href="#" className="header__right-btn">
                Добавить обьявления
              </a>
            </div>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
